import interfaces from '../../utils/interfaces.js';
import app from "../../App.vue";
export default {
	data() {
		return {
			data: [],
			user_info: '',
			token: ''
		}
	},
	onShow() {
		this.user_info = app.globalData.user_info;
		if (this.user_info) {
			this.token = app.globalData.user_info.token;
			this.request({
				url: interfaces.adrList,
				header: {
					token: this.token
				},
				method: 'POST',
				success: res => {
					console.log(res);
					if (res.code == 200) {
						this.data = res.data;
					} else {
						app.isLogin(res.code, res.msg);
					}
				}
			});

		} else {
			wx.reLaunch({
				url: '/pages/auth/index',
			})
		}
	},

	methods: {
		//点击添加地址事件,跳转到添加地址页面
		addAdr() {
			wx.navigateTo({
				url: 'add',
			})
		},

		//点击地址,选中该地址后，返回到提交订单页面
		selectAdr(id) {
			this.request({
				url: interfaces.setDefault,
				header: {
					token: this.token
				},
				data: {
					id: id
				},
				method: 'POST',
				success: res => {
					console.log(res);
					if (res.code == 200) {
						wx.navigateBack({
							delta: 1
						})
					} else {
						app.isLogin(res.code, res.msg);
					}
				}
			});
		},
	}
}
